<!--
 * @Description: 分销员结算查询
 * @Author: your name
 * @Date: 2019-11-28 
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="app-container">
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="分销员昵称：">
            <el-input v-model="list.userName"
                      clearable
                      placeholder="分销员昵称"></el-input>
          </el-form-item>
          <el-form-item label="分销员手机号：">
            <el-input v-model="list.cellPhone"
                      clearable
                      placeholder="分销员手机号"></el-input>
          </el-form-item>
          <el-form-item label="订单号：">
            <el-input v-model="list.orderNo"
                      clearable
                      placeholder="订单号"></el-input>
          </el-form-item>
          <el-form-item label="结算状态：">
            <el-select v-model="list.setState"
                       placeholder="请选择">
              <el-option v-for="item in settlementList"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="按分组查看：">
            <el-select v-model="list.groupId"
                       clearable
                       placeholder="默认选择全部">
              <el-option v-for="item in groupList"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="下单时间：">
            <el-date-picker v-model="list.registerTime"
                            type="datetimerange"
                            value-format="yyyy-MM-dd HH-mm-ss"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结算时间：">
            <el-date-picker v-model="list.setTime"
                            type="datetimerange"
                            value-format="yyyy-MM-dd HH-mm-ss"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>

    <el-card class="operate-container"
             shadow="never">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                border>
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="订单号"
                         width="200"
                         align="center">
          <template slot-scope="scope">
            <span style="color:#409EFF"
                       @click="$router.push({path:'/order/orderList/orderinfo',query:{orderId:scope.row.orderId}})">
              {{scope.row.orderNo}}
            </span>
          </template>
        </el-table-column>
        <el-table-column label="下单日期"
                         width="190"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="结算时间"
                         prop="setTime"
                         width="190"
                         align="center">
        </el-table-column>
        <el-table-column label="分销商品实付金额"
                         width="155"
                         align="center">
          <template slot-scope="scope">￥{{ scope.row.payAmount }}</template>
        </el-table-column>
        <el-table-column label="佣金（对应分销员信息）">
          <el-table-column label="分销员昵称"
                           prop="userName"
                           width="155"
                           align="center">
          </el-table-column>
          <el-table-column label="分销员电话"
                           width="155"
                           prop="cellPhone"
                           align="center">
          </el-table-column>
          <el-table-column label="所属分组"
                           width="155"
                           prop="groupName"
                           align="center">
          </el-table-column>
          <el-table-column label="佣金奖励"
                           width="155"
                           prop="commissionAmount"
                           align="center">
          </el-table-column>
          <!-- prop="commissionInfo"
                         width="450"
                         align="center"> -->
        </el-table-column>
        <el-table-column label="邀请奖励（对应分销员信息）">
          <el-table-column label="分销员昵称"
                           prop="channelUserName"
                           width="155"
                           align="center">
          </el-table-column>
          <el-table-column label="分销员电话"
                           width="155"
                           prop="channelCellPhone"
                           align="center">
          </el-table-column>
          <el-table-column label="所属分组"
                           width="155"
                           prop="channelGroupName"
                           align="center">
          </el-table-column>
          <el-table-column label="邀请奖励"
                           width="155"
                           prop="inviteAmount"
                           align="center">
          </el-table-column>
          <!-- width="450"
                         prop="inviteInfo"
                         align="center"> -->
        </el-table-column>
        <el-table-column label="结算状态"
                         width="120"
                         align="center">
          <template slot-scope="scope">
            <el-tag type="danger"
                    v-if="scope.row.setStateName == '待结算'">待结算</el-tag>
            <el-tag type="success"
                    v-if="scope.row.setStateName == '已结算'">已结算</el-tag>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end"
           style="margin-top:20px">
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>

  </div>
</template>
<script>
import { mapState } from 'vuex'
import {
  initChannelGroupList
} from '@/api/distributionManagement/distributor'
import { channelOrderList } from '@/api/distributionManagement/performance'
export default {
  data () {
    return {
      list: {
        userName: '',
        cellPhone: '',
        setState: '',
        orderNo: '',
        groupId: '',
        registerTime: '',
        setTime: '',
      },
      groupList: [],
      id: '',
      tableList: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      settlementList: [
        {
          value: '0',
          label: '全部'
        },
        {
          value: '1',
          label: '未结算'
        },
        {
          value: '2',
          label: '已结算'
        }
      ],
    }
  },
  created () {
    this.searchList()
    this.searchGroupList()
  },
  mounted () {

  },
  methods: {

    //查询分销员分组
    searchGroupList () {
      initChannelGroupList({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.groupList = res.data
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //查询数据列表
    searchList () {
      this.loading = true
      channelOrderList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        userName: this.list.userName,
        cellPhone: this.list.cellPhone,
        orderNo: this.list.orderNo,
        setState: this.list.setState,
        groupId: this.list.groupId,
        beginTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
        setBeginTime: this.list.setTime == null ? '' : this.list.setTime[0],
        setEndTime: this.list.setTime == null ? '' : this.list.setTime[1],
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          });
        }
      })
    },
    //重置
    reset () {
      this.list = {
        userName: '',
        cellPhone: '',
        setState: '',
        orderNo: '',
        groupId: '',
        registerTime: '',
        setTime: '',
      }
      this.pageNum = 1
      this.searchList()
    },


    //修改每页页数
    handleSizeChange (page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange (page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>

<style lang="scss" scoped>
.operate-container {
  margin-top: 10px;
}
.app-container {
  padding: 20px;
}
.btn-add {
  float: right;
  margin-top: -8px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
</style>
